@import url("https://fonts.googleapis.com/css?family=Muli&display=swap");
@import "~normalize.css";
@import "base/variables";
@import 'base/mixins';
@import "blocks/square";
@import "blocks/block";
@import "blocks/checkbox";

* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin: 0;
}

.section {
  position: absolute;
  top: 1px;
  left: auto;
  width: 100%;
  height: 100%;
  // pointer-events: none;
  border: solid 1px red;
}

.hidden {
  display: none;
}

input:focus {
  border-color: $color-orange-light;
  border-radius: 5px;
  outline: 0;
}
